<template>
  <div>
    <!-- jumbotron + carousel -->
    <b-jumbotron>
      <b-carousel
        id="carousel-1"
        v-model="slide"
        :interval="3000"
        controls
        indicators
        background="black"
      >

        <b-carousel-slide>
          <h3>Snowboard</h3>
          <p class="carotext">Your best and important friend for snowboarding.</p>
          <template v-slot:img>
            <img
              class="d-block"
              width="512"
              height="340"
              src="../assets/img4.jpg"
              alt="image slot"
            >
          </template>
        </b-carousel-slide>

        <b-carousel-slide>
          <h3>Clothes</h3>
          <p class="carotext">Keep your body warm and protect your body.</p>
          <template v-slot:img>
            <img
              class="d-block"
              width="512"
              height="340"
              src="../assets/img5.jpg"
              alt="image slot"
            >
          </template>
        </b-carousel-slide>

        <b-carousel-slide>
          <h3>Goggles</h3>
          <p class="carotext">Protect your eyes and let you see clearly.</p>
          <template v-slot:img>
            <img
              class="d-block"
              width="512"
              height="340"
              src="../assets/img6.jpg"
              alt="image slot"
            >
          </template>
        </b-carousel-slide>

      </b-carousel>
      <b-button
        v-b-modal.modal-center
        variant="info"
      >Cart ({{ cartNumber }})</b-button>
    </b-jumbotron>

    <!-- modal + table -->
    <b-modal
      ref="modal-center"
      id="modal-center"
      centered
      title="Cart"
    >
      <b-table
        :items="showItems"
        :fields="fields"
      >
        <template v-slot:cell(Check)="row">
          <b-button variant="danger" @click="deleteHandler(row.index, row.item)">
            Delete
          </b-button>
        </template>
      </b-table>

      <b-alert
        show
        variant="success"
      >Total price：{{ totalPrice }} NTD</b-alert>

      <template v-slot:modal-footer>
        <b-button variant="info" @click="checkoutHandler(totalPrice)">Checkout</b-button>
        <b-button variant="secondary" @click="$bvModal.hide('modal-center')">Cancel</b-button>
      </template>
    </b-modal>

    <!-- card-group -->
    <b-card-group deck>
      <b-card
        v-for="(item, index) in items"
        :key="index"
      >
        <b-card-img :src="item.src"></b-card-img>
        <div class="mycard">
          <h2>{{item.Item}}</h2>
          <h5>
            <b-badge
              v-if="item.discount === true"
              variant="danger"
            >特價：{{item.Price}} NTD</b-badge>
            <b-badge
              v-else
              variant="success"
            >售價：{{item.Price}} NTD</b-badge>
          </h5>
          <b-card-text>
            {{item.text}}
          </b-card-text>
        </div>
        <template v-slot:footer>
          <b-row
            class="text-center"
            no-gutters
          >
            <b-col>
              <b-button
                variant="outline-info"
                @click="minusHandler(index)"
              >－</b-button>
            </b-col>
            <b-col>
              <b-button
                disabled
                variant="light"
                class="number"
              >{{ item.Number }}</b-button>
            </b-col>
            <b-col>
              <b-button
                variant="outline-info"
                @click="plusHandler(index)"
              >＋</b-button>
            </b-col>
            <b-col>
              <b-button variant="outline-info" @click="buyHandler(index)">Buy</b-button>
            </b-col>
          </b-row>
        </template>
      </b-card>

    </b-card-group>
  </div>
</template>

<script>
export default {
  data () {
    return {
      slide: 0,
      cart: 0,
      fields: ['#', 'Item', 'Price', 'Number', 'Total', 'Check'],
      items: JSON.parse(localStorage.getItem('items')) || [
        {
          '#': 1,
          src: require('@/assets/img4.jpg'),
          Item: 'Snowboard',
          Price: 6999,
          discount: true,
          text: 'Your best and important friend for snowboarding.',
          Number: 0,
          Total: 0,
          alreadyBuy: false
        },
        {
          '#': 2,
          src: require('@/assets/img5.jpg'),
          Item: 'Clothes',
          Price: 2000,
          discount: false,
          text: 'Keep your body warm and protect your body.',
          Number: 0,
          Total: 0,
          alreadyBuy: false
        },
        {
          '#': 3,
          src: require('@/assets/img6.jpg'),
          Item: 'Goggles',
          Price: 780,
          discount: false,
          text: 'Protect your eyes and let you see clearly.',
          Number: 0,
          Total: 0,
          alreadyBuy: false
        }
      ],
      showItems: JSON.parse(localStorage.getItem('showItems')) || []
    }
  },
  methods: {
    minusHandler (index) {
      if (this.items[index].Number !== 0) {
        this.items[index].Number--
      }
    },
    plusHandler (index) {
      this.items[index].Number++
    },
    buyHandler (outsideIndex) {
      if (this.items[outsideIndex].alreadyBuy === false) {
        this.items[outsideIndex].alreadyBuy = true
        this.items[outsideIndex]['#'] = this.showItems.length + 1
        this.items[outsideIndex].Total = this.items[outsideIndex].Price * this.items[outsideIndex].Number
        let newItems = Object.assign({}, this.items[outsideIndex])
        this.showItems.push(newItems)
      } else {
        this.showItems.forEach((showItem, index) => {
          if (showItem.Item === this.items[outsideIndex].Item) {
            this.showItems[index].Number += this.items[outsideIndex].Number
            this.showItems[index].Total = this.showItems[index].Price * this.showItems[index].Number
          }
        })
      }
      this.items[outsideIndex].Number = 0
      localStorage.setItem('items', JSON.stringify(this.items))
      localStorage.setItem('showItems', JSON.stringify(this.showItems))
    },
    deleteHandler (deleteIndex, deleteItem) {
      if (confirm(`Do you really want to delete the item of " ${deleteItem.Item} " ？`)) {
        this.items.forEach((item, index) => {
          if (item.Item === deleteItem.Item) {
            this.items[index].alreadyBuy = false
          }
        })
        this.showItems.splice(deleteIndex, 1)
        this.showItems.forEach((showItem, index) => {
          if (showItem['#'] > deleteIndex + 1) {
            this.showItems[index]['#']--
          }
        })
      }
      localStorage.setItem('items', JSON.stringify(this.items))
      localStorage.setItem('showItems', JSON.stringify(this.showItems))
    },
    checkoutHandler (totalPrice) {
      alert(`Has been deducted ${totalPrice} dollars from your credit card！`)
      this.showItems = []
      this.items.forEach((item, index) => {
        this.items[index].alreadyBuy = false
      })
      this.$refs['modal-center'].hide()
      localStorage.setItem('items', JSON.stringify(this.items))
      localStorage.setItem('showItems', JSON.stringify(this.showItems))
    }
  },
  computed: {
    cartNumber () {
      let count = 0
      this.items.forEach((item) => {
        if (item.alreadyBuy === true) {
          count++
        }
      })
      return count
    },
    totalPrice () {
      let total = 0
      this.showItems.forEach((showItem) => {
        total += showItem.Total
      })
      return total
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .d-block {
    margin: auto;
    opacity: 0.6;
  }
  h3,
  .carotext {
    font-family: monospace;
  }
  h3 {
    font-size: 2.15rem;
  }
  .jumbotron {
    padding: 0;
    margin-bottom: 1rem;
  }
  .card-body {
    padding: 0;
  }
  .mycard {
    padding: 20px;
  }
  h2 {
    margin-bottom: 6px;
  }
  .badge {
    line-height: 1.1;
  }
  h5 {
    font-size: 20px;
  }
  .card-footer {
    font-family: monospace;
    padding: 0;
  }
</style>
<style>
  .modal-title {
    font-family: monospace;
    font-size: 30px;
  }
  .modal-body {
    padding: 0;
  }
  .table {
    text-align: center;
    font-size: 17px;
    font-family: monospace;
    margin: 0;
  }
  .alert {
    font-size: 16px;
    font-family: monospace;
    margin: 0;
    text-align: right;
  }
</style>



